<!--        
    1. :class="{fixedWrapper:isShowmap, scrollWrapper:!isShowmap}"
    2.   ref="showMap" v-on:change="change()"  
    3.     
        <button v-on:click="this.$refs.showMap.showMap(), change()"></button>
    4. isShowmap : false change()  
    5. .fixedWrapper .scrollWrapper   -->
<template>
    <div  :class="{visibleBox:openMap, invisibleBox:!openMap}">
      <div class="white-bg">
        <h4>  </h4>
        <div id="map" ref="map"></div>
        <p>{{this.take_lok}}</p>
        <button v-on:click="closeMap" class="close"></button>
      </div>
    </div>
</template>

<script>
export default {
    data(){
      return{
        openMap: false,
        take_loc: this.$store.getters.GET_DELIVERY_POST.take_loc,      //   
        latitude: this.$store.getters.GET_DELIVERY_POST.lat,
        longtitude: this.$store.getters.GET_DELIVERY_POST.lon
      }
    },
    mounted(){
        let $vm = this;
        let kakao = window.kakao;
        // var mapContainer = document.getElementById('map'), //   div 
        var mapContainer = this.$refs.map,
        mapOption = { 
            center: new kakao.maps.LatLng($vm.latitude, $vm.longtitude), //  
            level: 3, //   
            mapTypeId : kakao.maps.MapTypeId.ROADMAP // 
        };
        console.log(mapOption);
        console.log($vm.latitude, $vm.longtitude);

        var map = new kakao.maps.Map(mapContainer, mapOption); //  
        //    
        var markerPosition  = new kakao.maps.LatLng($vm.latitude, $vm.longtitude); 

        //  
        var marker = new kakao.maps.Marker({
            position: markerPosition
        });
        //     
        marker.setMap(map); 
    },
    methods:{
      showMap(){
        this.openMap = true;
      },
      closeMap(){
        this.openMap = false;
      },
    }
}
</script>

<style>
.wrapper{
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#map{
    width: 95%;
    height: 400px;
    margin: 0 auto;
    border-radius: 20px;
}

.visibleBox{
  visibility: visible;
  z-index: 1050;
  width:100%;
  height:100%;
  background: rgba(0,0,0,0.6);
  position: fixed;
}
.invisibleBox{
  visibility: hidden;
  width:100%;
  height:100%;
  position: fixed;
}
.invisibleBox h4{
  margin: 12px auto;
}
.visibleBox h4{
  margin: 12px auto;
}

.white-bg{
    width: 50%;
    margin: 170px auto;
    background: white;
    border-radius: 62px;
    padding: 15px 0;
}

.close{
  cursor:pointer;
  border:none;
  background:#ccc;
  color:white;
  font-weight: bold;
  border-radius: 10px;
  padding: 5px 15px;
}

.close:hover{
  color:white;
  font-weight: bold;
  transform: scale(1.1);
  transition: all 0.5s;
}
</style>